<!--
       * Todo: Property 작성 하는 부분 *
-->
<template>
<!-- width 390 -->
    <v-layout wrap>
        <v-navigation-drawer v-model="value.drawer" absolute right v-bind:style="{width: widthScale}" >

            <!--  상단 이미지 및 선택 타이틀 이름-->
            <v-list class="pa-1">
                <v-list-item>
                    <v-list-item-avatar>
                        <img :src="img">
                    </v-list-item-avatar>
                    <v-list-item-title class="headline">{{ titleName }}
                    </v-list-item-title>
                    <v-tooltip v-model="show" top>
                        <template v-slot:activator="{ on }">
                            <v-btn icon v-on="on">
                                <v-icon color="grey lighten-1">mdi-information</v-icon>
                            </v-btn>
                        </template>
                        <span>{{value.description_text}}</span>
                    </v-tooltip>
                </v-list-item>
            </v-list>

            <!--  해당 판넬의 내용  -->
            <v-list class="pt-0" dense flat>
                <v-divider></v-divider>
                <v-card v-if="value._type == 'org.uengine.model.Relation' && value.sourceElement._type == 'org.uengine.model.Event'">
                    <v-card-text>
                        <span class="headline">Type</span>
                        <v-layout>
                            <v-col>
                                <v-radio-group v-model="value.relationKey" style="padding-left: 10px;">
                                    <v-radio label="Event-Driven(Pub/Sub)" value="RestAPI"></v-radio>
                                    <v-radio label="Request & Response(Restful)" value="Restful"></v-radio>
                                </v-radio-group>
                                <v-select :disabled="value.relationKey == 'RestAPI'" v-model="value.relationValue"
                                          :items="restfulList"
                                          label="Request & Response(Restful)"></v-select>
                            </v-col>
                        </v-layout>
                    </v-card-text>
                </v-card>
                <v-card outlined v-else>
                    <v-card-text>
                        <span class="headline" v-if="titleName">{{titleName}} Name</span>
                        <v-textarea name="input-7-1" outline :label="'Input Text'" auto-grow
                                    v-model="value.name"></v-textarea>

                        <v-card outlined v-if="usedTranslate">
                            <v-card-text @click="changeTranslate()">
                                추천 단어 : {{ translateText }}
                            </v-card-text>
                            <v-card-text>
                                선택시 변경 됩니다.
                            </v-card-text>
                        </v-card>


                        <span class="headline" v-if="value._type == 'org.uengine.model.Event'">Trigger</span>
                        <v-layout v-if="value._type == 'org.uengine.model.Event'" flat>
                            <v-radio-group v-model="value.trigger" style="font-size: small">
                                <v-row>
                                    <v-col>
                                        <v-radio label="Pre Persist" value="@PrePersist"></v-radio>
                                        <v-radio label="Pre Update" value="@PreUpdate"></v-radio>
                                        <v-radio label="Pre Delete" value="@PreDelete"></v-radio>
                                    </v-col>
                                    <v-col>
                                        <v-radio label="Post Persist" value="@PostPersist"></v-radio>
                                        <v-radio label="Post Update" value="@PostUpdate"></v-radio>
                                        <v-radio label="Post Delete" value="@PostDelete"></v-radio>
                                    </v-col>
                                </v-row>
                            </v-radio-group>
                        </v-layout>


                        <span class="headline"
                              v-if="value._type == 'org.uengine.model.Event' ||
                              value._type == 'org.uengine.model.Aggregate'">Attributes</span>
                        <v-layout
                                v-if="value._type == 'org.uengine.model.Event'"
                                flat>
                            <v-col>
                                <v-data-table
                                        :headers="headers"
                                        :items="value.fieldDescriptors"
                                        hide-default-header
                                        hide-default-footer
                                        class="elevation-1"
                                >

                                    <template v-slot:item.action="{ item }">
                                        <v-icon
                                                small
                                                @click="deleteEntity(item)"
                                        >
                                            delete
                                        </v-icon>
                                    </template>
                                </v-data-table>


                                <v-row justify="center">
                                    <v-flex xs4>
                                        <v-select v-model="entityType" :items="entityTypeList" label="Type"
                                                  style="margin-right: 15px;"></v-select>
                                    </v-flex>
                                    <v-flex xs6>
                                        <v-text-field v-model="entityName" label="Name"
                                                      v-on:keyup.enter="attributeADD(entityType,entityName)"
                                                      required></v-text-field>
                                    </v-flex>
                                </v-row>
                                <v-row justify="end">
                                    <v-btn rounded color="primary" @click="attributeADD(entityType,entityName)" dark>
                                        ADD ATTRIBUTE
                                    </v-btn>
                                </v-row>
                            </v-col>
                        </v-layout>

                        <v-layout
                                v-else-if="value._type == 'org.uengine.model.Aggregate'"
                                flat>
                            <v-col>
                                <v-data-table
                                        :headers="headers"
                                        :items="value.aggregateRoot.fieldDescriptors"
                                        hide-default-header
                                        hide-default-footer
                                        class="elevation-1"
                                >

                                    <template v-slot:item.action="{ item }">
                                        <v-icon
                                                v-if="item.name != 'id'"
                                                small
                                                @click="deleteEntity(item)"
                                        >
                                            delete
                                        </v-icon>
                                    </template>
                                </v-data-table>


                                <v-row justify="center">
                                    <v-flex xs4>
                                        <v-select v-model="entityType" :items="entityTypeList" label="Type"
                                                  style="margin-right: 15px;"></v-select>
                                    </v-flex>
                                    <v-flex xs6>
                                        <v-text-field v-model="entityName" label="Name"
                                                      v-on:keyup.enter="attributeADD(entityType,entityName)"
                                                      required></v-text-field>
                                    </v-flex>
                                </v-row>
                                <v-row justify="end">
                                    <v-btn rounded color="primary" @click="attributeADD(entityType,entityName);" dark>
                                        ADD ATTRIBUTE
                                    </v-btn>
                                </v-row>
                            </v-col>
                        </v-layout>

                        <v-layout v-if="value._type == 'org.uengine.model.Command'">
                            <v-autocomplete v-model="value.restfulType" :items="restfulList"
                                            label="Restful TYPE" persistent-hint>
                            </v-autocomplete>
                        </v-layout>


                        <span class="headline"
                              v-if="value._type == 'org.uengine.model.Event' || value._type == 'org.uengine.model.Policy' || value._type == 'org.uengine.model.Command' || value._type == 'org.uengine.model.External'">Associated Aggregate</span>
                        <v-layout
                                v-if="value._type == 'org.uengine.model.Event' || value._type == 'org.uengine.model.Policy' || value._type == 'org.uengine.model.Command' || value._type == 'org.uengine.model.External'"
                                flat>
                            <v-col>
                                <v-autocomplete
                                        v-model="value.aggregateId"
                                        style="margin-left: 20px; margin-right: 20px;"
                                        :items="aggregateList"
                                        item-text="name"
                                        item-value="id"
                                        label="Select Aggregate" persistent-hint>
                                </v-autocomplete>
                            </v-col>
                        </v-layout>
                        <v-layout
                            v-if="value._type == 'org.uengine.model.View'"
                        >
                            <v-card-text>
                                <span class="headline">Type</span>
                                <v-layout>
                                    <v-col>
                                        <v-autocomplete
                                                v-model="value.aggregateId"
                                                style="margin-left: 20px; margin-right: 20px;"
                                                :items="aggregateList"
                                                item-text="name"
                                                item-value="id"
                                                label="Select Aggregate" persistent-hint>
                                        </v-autocomplete>

                                        <v-row>
                                        <v-radio-group v-model="value.dataProjection" style="padding-left: 10px; " row>
                                            <v-radio label="CQRS" value="cqrs"></v-radio>
                                            <v-radio label="UI Mashup" value="uimashup"></v-radio>
<!--                                            <v-radio label="GraphQL" value="graphql"></v-radio>-->
                                        </v-radio-group>
                                            <v-row>
                                            <v-btn text  v-if="value.editingView == false" @click="value.editingView = true"> EDIT</v-btn>
                                            <v-btn text  v-if="value.editingView == true" @click="value.editingView = false"> SAVE</v-btn>
                                            </v-row>
                                        </v-row>

                                        <v-data-table
                                                :headers="value.viewHeaders"
                                                :items="value.fieldDescriptors"
                                                item-key="name"
                                                class="elevation-1"

                                        >


                                            <template   v-slot:item.key="{ item }">
                                                <div v-if="value.editingView">
                                                    <v-checkbox
                                                            v-model="item.key"
                                                            type="text"
                                                    ></v-checkbox>
                                                </div>
                                                <div v-if="!value.editingView" >
                                                    {{item.key}}
                                                </div>
                                            </template>

                                            <template v-if="value.editingView"  v-slot:item.columnName="{ item }">
                                                <div >
                                                    <v-select v-model="item.columnName" :items="colors" label="columnName"></v-select>
                                                </div>
                                            </template>

                                            <template v-if="value.editingView"  v-slot:item.type="{ item }">
                                                <div >
                                                    <v-select v-model="item.type" :items="entityTypeList" label="type"></v-select>
                                                </div>
                                            </template>
                                            <template v-if="value.editingView"  v-slot:item.sourceEvent="{ item }">
                                                <div >
                                                    <v-select v-model="item.sourceEvent" :items="entityTypeList" label="sourceEvent"></v-select>
                                                </div>
                                            </template>
                                            <template v-if="value.editingView"  v-slot:item.protein="{ item }">
                                                <div >
                                                    <v-select v-model="item.protein" :items="colors" label="Color"></v-select>
                                                </div>
                                            </template>
                                            <template v-if="value.editingView"  v-slot:item.viewColumnName="{ item }">
                                                <div >
                                                    <v-select v-model="item.viewColumnName" :items="colors" label="viewColumnName"></v-select>
                                                </div>
                                            </template>
                                            <template v-if="value.editingView"  v-slot:item.sourceEventColumn="{ item }">
                                                <div >
                                                    <v-select v-model="item.sourceEventColumn" :items="colors" label="sourceEventColumn"></v-select>
                                                </div>
                                            </template>

                                            <template v-if="value.editingView"  v-slot:item.sourceRepository="{ item }">
                                                <div >
                                                    <v-select v-model="item.sourceRepository" :items="colors" label="sourceRepository"></v-select>
                                                </div>
                                            </template>

                                            <template v-if="value.editingView"  v-slot:item.hateoas="{ item }">
                                                <div >
                                                    <v-select v-model="item.hateoas" :items="colors" label="hateoas"></v-select>
                                                </div>
                                            </template>

                                            <template v-if="value.editingView"  v-slot:item.link="{ item }">
                                                <div >
                                                    <v-select v-model="item.link" :items="colors" label="link"></v-select>
                                                </div>
                                            </template>

                                            <template v-slot:item.action="{ item }">
                                                <v-icon
                                                        small
                                                        @click="deleteItem(item)"
                                                >
                                                    delete
                                                </v-icon>
                                            </template>


                                            <template  v-if="value.editingView" v-slot:body.append>
                                                <v-btn text block @click="viewRowAdd()"><v-icon>mdi-plus</v-icon></v-btn>
                                            </template>
                                        </v-data-table>
                                    </v-col>
                                </v-layout>
                            </v-card-text>

                        </v-layout>
                        <v-layout v-else-if="value._type == 'org.uengine.model.Aggregate'">
                            <v-divider dark style="margin-top: 10px; margin-bottom: 10px;"></v-divider>
                            <v-btn block color="info" rounded @click="umlDiagramOpen()"> Edit Domain Model by UML
                            </v-btn>
                        </v-layout>

                    </v-card-text>
                </v-card>
            </v-list>
        </v-navigation-drawer>
    </v-layout>
</template>

<script>
    var _ = require('lodash')
    var googleTranslate = require('google-translate')(process.env.VUE_APP_TRANSLATE_KEY);
    var changeCase = require('change-case');
    var pluralize = require('pluralize');
    //https://www.npmjs.com/package/change-case

    export default {
        name: 'modeling-property-panel',
        props: {
            value: Object,
            titleName: String,
            img: String,
        },
        computed: {
            widthScale: function () {
                var me = this;
                if(me.value._type == 'org.uengine.model.View') {
                    return 500
                } else {
                    return 390
                }
            },
            aggregateList: function () {
                var designer = this.$parent.getComponent('modeling-designer')

                var tmp = []

                designer.value.definition.forEach(function (tmpData) {
                    if (tmpData._type == 'org.uengine.model.Aggregate')

                        var ob = {
                            'name': tmpData.name,
                            'id': tmpData.elementView.id
                        }
                    tmp.push(ob)
                })


                tmp = tmp.filter(n => n)
                return tmp
            },
        },
        data: function () {
            return {
                show: false,
                navigationDrawer: false,
                _item: this.value,
                preventWatch: false,
                x: null,
                y: null,
                width: null,
                height: null,
                style: [],
                angle: null,
                connectedList: [],
                connectedListName: {},
                viewList:null,
                restfulList: ['GET', 'POST', 'PATCH', 'DELETE'],
                entityTypeList: ['Integer', 'String', 'Float', 'Double', 'Long'],
                entityType: '',
                entityName: '',
                headers: [
                    {
                        text: 'type',
                        value: 'className',
                        align: 'center'
                    },
                    {
                        text: 'name',
                        value: 'name',
                        align: 'center'
                    },
                    {
                        text: 'Actions',
                        value: 'action',
                        sortable: false,
                        align: 'center'
                    },],
                translateText: '',
                usedTranslate: false,
                UMLInput: '',
                temporaryList: [],

            }
        },
        created: function () {

        },
        mounted: function () {
        },
        watch: {
            "value.relationKey": function (newVal) {
                var me = this

                // me.value.relationValue = newVal
                if (newVal == 'RestAPI') {
                    me.value.relationValue = 'Pub/Sub'
                }
                me.value.sourceElement.eventToPolicy = me.value.relationValue
                me.value.targetElement.eventToPolicy = me.value.relationValue
                //Policy value Setting
                me.value.targetElement.relationEventInfo=me.value.sourceElement;

            },
            "value.name": function (newVal) {
                var me = this

                me.translateText = '';
                me.getTranslate(newVal)
                me.setNameCase(newVal)

            },
            "value.drawer": function (val) {
                this.navigationDrawer = val;
            },
            //프로퍼티 창이 오픈되었을 때 모델값을 새로 반영한다.
            navigationDrawer: {
                handler: function (val, oldval) {
                    var opengraph = this.$parent.getComponent('opengraph')
                    if (this.titleName == 'Aggregate') {
                        // this.getRelation()
                    }
                    if (val == true) {
                        this._item = this.value;

                        if (this.value.elementView) {
                            this.x = this.value.elementView.x;
                            this.y = this.value.elementView.y;
                            this.width = this.value.elementView.width;
                            this.height = this.value.elementView.height;
                        }
                        this.$emit('update:drawer', val);

                    } else {
                        //프로퍼티 에디팅 해제.
                        this.$emit('update:drawer', false);
                    }
                }
            },
            x: function (val) {
                this._item.elementView.x = val;
                this.$emit('update:value', this._item);
            },
            y: function (val) {
                this._item.elementView.y = val;
                this.$emit('update:value', this._item);
            },
            width: function (val) {
                this._item.elementView.width = val;
                this.$emit('update:value', this._item);
            },
            height: function (val) {
                this._item.elementView.height = val;
                this.$emit('update:value', this._item);
            },
            angle: function (val) {
                this._item.elementView.angle = val;
                this.$emit('update:value', this._item);
            },
            style: {
                handler: function (newVal, oldVal) {
                    var style = {};
                    if (newVal && newVal.length) {
                        $.each(newVal, function (i, item) {
                            style[item.key] = item.value;
                        });
                    }
                    var view = this._item.elementView || this._item.relationView;
                    view.style = JSON.stringify(style);
                    this.$emit('update:value', this._item);
                },
                deep: true
            },
            "value.AggregateName": function (val) {

                var designer = this.$parent.getComponent('modeling-designer')
                console.log(designer)

            }
        },
        mounted: function () {

        },
        methods: {
            save () {
                var me = this

                if (this.editedIndex > -1) {
                    Object.assign(me.value.fieldDescriptors[this.editedIndex], this.editedItem)
                } else {
                    me.value.fieldDescriptors.push(this.editedItem)
                }
                me.value.editing =false
            },
            editItem (item) {
                var me  =  this
                this.editedIndex = me.value.fieldDescriptors.indexOf(item)
                this.editedItem = Object.assign({}, item)
                me.value.editing  = true
            },

            deleteItem (item) {
                var me =  this
                const index = me.value.fieldDescriptors.indexOf(item)
                confirm('Are you sure you want to delete this item?') && me.value.fieldDescriptors.splice(index, 1)
            },
            viewRowAdd() {
               var me = this
                if(me.value.dataProjection == 'cqrs'){
                    var ob ={
                        key:'',
                        columnName: '',
                        type: '',
                        SourceEvent:'',
                        protein:'',
                        viewColumnName:'',
                        sourceEventColumn:''
                    }
                    me.value.fieldDescriptors.push(ob);

                }else if(me.value.dataProjection == 'uimashup'){
                    var ob ={
                        key:'',
                        columnName: '',
                        type: '',
                        SourceRepository:'',
                        protein:'',
                        hateoas:'',
                        link:''
                    }
                    me.value.fieldDescriptors.push(ob);
                }
            },
            setNameCase: _.debounce(
                function (newVal) {
                    var me = this
                    if (me.value._type == 'org.uengine.model.Event' || me.value._type == 'org.uengine.model.Command' || me.value._type == 'org.uengine.model.Policy' || me.value._type == 'org.uengine.model.Aggregate') {
                        me.value.namePascalCase = changeCase.pascalCase(newVal)
                        me.value.nameCamelCase = changeCase.camelCase(newVal)
                        me.value.namePlural = pluralize(newVal);
                        console.log(me.value.namePlural, me.value.namePascalCase, me.value.nameCamelCase)
                    }
                }, 200
            ),
            getTranslate: _.debounce(
                function (newVal) {
                    var me = this
                    if (me.value._type == 'org.uengine.model.Event') {
                        try {
                            googleTranslate.detectLanguage(newVal, function (err, detection) {
                                if (detection.language != 'en') {
                                    googleTranslate.translate(newVal, 'en', function (err, translation) {
                                        me.usedTranslate = true
                                        me.translateText = changeCase.pascalCase(translation.translatedText);
                                    });
                                }
                            });
                        } catch (e) {
                            console.log(e)
                        }

                    } else if (me.value._type == 'org.uengine.model.Policy') {

                        try {
                            googleTranslate.detectLanguage(newVal, function (err, detection) {
                                if (detection.language != 'en') {
                                    googleTranslate.translate(newVal, 'en', function (err, translation) {
                                        me.usedTranslate = true
                                        me.translateText = changeCase.pascalCase(translation.translatedText);
                                    });
                                } else if (detection.language == 'en') {
                                    me.usedTranslate = true
                                    me.translateText = changeCase.pascalCase(newVal)
                                }
                            });
                        } catch (e) {
                            console.log(e)
                        }

                    } else if (me.value._type == 'org.uengine.model.Aggregate') {

                        try {
                            googleTranslate.detectLanguage(newVal, function (err, detection) {
                                if (detection.language != 'en') {
                                    googleTranslate.translate(newVal, 'en', function (err, translation) {
                                        me.usedTranslate = true
                                        me.translateText = changeCase.pascalCase(translation.translatedText);
                                    });
                                } else if (detection.language == 'en') {
                                    me.usedTranslate = true
                                    me.translateText = changeCase.pascalCase(newVal)
                                }
                            });
                        } catch (e) {
                            console.log(e)
                        }

                    } else {
                        try {
                            googleTranslate.detectLanguage(newVal, function (err, detection) {
                                if (detection.language != 'en') {
                                    googleTranslate.translate(newVal, 'en', function (err, translation) {
                                        me.usedTranslate = true
                                        me.translateText = translation.translatedText;
                                    });
                                }
                            });
                        } catch (e) {
                            console.log(e)
                        }

                    }
                }, 500
            ),
            umlDiagramOpen() {
                var me = this
                console.log("umlDiagram-Open")
                me.$ModelingBus.$emit('umlDiagram');
            },
            deleteEntity(val) {
                var me = this

                if (me.value._type == 'org.uengine.model.Aggregate') {
                    me.value.aggregateRoot.fieldDescriptors.forEach(function (element, idx) {
                        if (element.name == val.name && element.type == val.type) {

                            if (idx > -1)
                                me.value.aggregateRoot.fieldDescriptors.splice(idx, 1)

                            // me.$emit('update:aggregateRoot.fieldDescriptors', me.aggregateRoot.fieldDescriptors);
                        }
                    })
                } else {

                    me.value.fieldDescriptors.forEach(function (element, idx) {
                        if (element.name == val.name && element.type == val.type) {

                            if (idx > -1)
                                me.value.fieldDescriptors.splice(idx, 1)

                            // me.$emit('update:fieldDescriptors', me.fieldDescriptors);
                        }
                    })

                }
            },
            changeTranslate() {
                this.value.name = this.translateText
                this.usedTranslate = false
            },
            attributeADD: function (type, name) {
                var me = this
                if(type.length < 1 ) {
                    type = 'String'
                }
                if (type.length != 0 && name.length != 0) {
                    let tmpObject = null

                    tmpObject = {
                        "_type": "org.uengine.model.FieldDescriptor",
                        "name": name,
                        "namePascalCase": changeCase.pascalCase(name),
                        "nameCamelCase": changeCase.camelCase(name),
                        "className": type,
                    }

                    if (name == 'id') {
                        tmpObject = {
                            "_type": "org.uengine.model.FieldDescriptor",
                            "name": name,
                            "namePascalCase": changeCase.pascalCase(name),
                            "nameCamelCase": changeCase.camelCase(name),
                            "className": type,
                            "isKey": true
                        }
                    }

                    if (me.value._type == "org.uengine.model.Aggregate") {
                        me.value.aggregateRoot.fieldDescriptors.push(tmpObject);
                    } else {
                        me.value.fieldDescriptors.push(tmpObject);

                    }

                    this.entityType = ""
                    this.entityName = ""

                } else {
                    var designer = this.$parent.getComponent('modeling-designer')
                    designer.text = "TYPE & NAME INPUT REQUEST"
                    designer.snackbar = true
                }
            },

        }
    }
</script>


<style lang="scss" rel="stylesheet/scss">
    .md-sidenav .md-sidenav-content {
        width: 400px;
    }

    .md-sidenav.md-right .md-sidenav-content {
        width: 600px;
    }
</style>
