<template>
    <div>
        <geometry-element
                selectable
                :movable="!value.editing"
                :resizable="!value.editing"
                deletable
                :angle.sync="value.elementView.angle"
                :id.sync="value.elementView.id"
                :x.sync="value.elementView.x"
                :y.sync="value.elementView.y"
                :width.sync="value.elementView.width"
                :height.sync="value.elementView.height"
                v-on:dblclick="showProperty"
                v-on:selectShape="selectedActivity"
                v-on:deSelectShape="deSelectedActivity"
                v-on:rotateShape="onRotateShape"
                :label.sync="value.name"
                :_style="{
                'label-angle':value.elementView.angle,
                'font-weight': 'bold','font-size': '16'
                }"
        >
            <!--v-on:dblclick="$refs['dialog'].open()"-->
            <geometry-rect
                    :_style="{
          'fill-r': 1,
          'fill-cx': .1,
          'fill-cy': .1,
          'stroke-width': 1.4,
          'stroke': '#5FC08B',
          fill: '#5FC08B',
          'fill-opacity': 1,
          r: '1'
        }"
            >
            </geometry-rect>

            <sub-elements>
                <!--title-->
                <text-element
                        :sub-width="'100%'"
                        :sub-height="titleH"
                        :sub-top="0"
                        :sub-left="0"
                        :text="value.classReference ? value.classReference : '<< View >>'">
                </text-element>
            </sub-elements>
        </geometry-element>


        <modeling-property-panel
                v-model="value"
                :titleName="'View'"
                :img="'https://raw.githubusercontent.com/kimsanghoon1/k8s-UI/master/public/static/image/event/view.png'"
        >
        </modeling-property-panel>

    </div>
</template>

<script>
    import Element from '../../modeling/Element'

    export default {
        mixins: [Element],
        name: 'view-definition',
        props: {},
        computed: {
            defaultStyle() {
                return {}
            },
            className() {
                return 'org.uengine.model.View'
            },
            createNew(elementId, x, y, width, height) {
                return {
                    description_text: '행위와 결정을 하기 위하여 유저가 참고하는 데이터\n' +
                        '(데이터 프로젝션이 필요 :  CQRS 등으로 수집)',
                    _type: this.className(),
                    name: '',
                    aggregateId:'',
                    aggregate:[],
                    namePascalCase: '',
                    namePlural: '',
                    elementView: {
                        '_type': this.className(),
                        'id': elementId,
                        'x': x,
                        'y': y,
                        'width': width,
                        'height': height,
                        'style': JSON.stringify({})
                    },
                    checkValue: false,
                    drawer: false,
                    selected: false,
                    editing: false,
                    editingView:false,
                    dataProjection:'',
                    viewHeaders:[],
                    fieldDescriptors:[],
                    fieldDescriptorsCqrs:[
                        {
                            key: true,
                            columnName: 'asd',
                            type: 'String',
                            sourceEvent:'aa',
                            protein:'ss',
                            viewColumnName:'12',
                            sourceEventColumn:'22'
                        }
                    ],
                    fieldDescriptorsMash:[
                        {
                            key:true,
                            columnName: '',
                            type: '',
                            sourceRepository:'',
                            protein:'',
                            hateoas:'',
                            link:''
                        }
                    ],
                }
            }
        },
        data: function () {
            return {
                itemH: 20,
                titleH: (this.value.classReference ? 60 : 30),
                reference: this.value.classReference != null,
                referenceClassName: this.value.classReference,
            };
        },
        created: function () {

        },
        watch: {
            "value.aggregateId": function (newVal, oldVal) {
                var me = this
                var designer = this.getComponent('modeling-designer')

                designer.value.definition.forEach(function (temp) {
                    if (oldVal != null)
                        if (temp._type == "org.uengine.model.Aggregate" && temp.elementView.id == oldVal) {
                            temp.viewes.forEach(function (vi, idx) {
                                if (vi.elementView.id == me.value.elementView.id) {
                                    if (idx > -1) temp.viewes.splice(idx, 1)
                                }
                            })
                        }

                    if (temp._type == "org.uengine.model.Aggregate" && temp.elementView.id == newVal) {
                        // me.value.aggregateName = temp.name
                        me.value.aggregate = JSON.parse(JSON.stringify(temp))
                        temp.viewes.push(me.value)
                    }

                })
            },
            "value.name": function (newVal, oldVal) {
                if (newVal != oldVal)
                    this.value.checkValue = true
                this.value.namePascalCase = newVal.charAt(0).toUpperCase() + newVal.slice(1)
            },
            "value.dataProjection":function (newVal) {
                var me = this

                if(newVal == 'cqrs'){
                    me.value.viewHeaders = [
                        {
                            text: 'Key',
                            align: 'left',
                            value: 'key',
                        },
                        { text: 'columnName', value: 'columnName'},
                        { text: 'type', value: 'type'  },
                        { text: 'sourceEvent', value: 'sourceEvent' },
                        { text: 'sourceEvent or DirectValue', value: 'protein' },
                        { text: 'viewColumnName', value: 'viewColumnName' },
                        { text: 'sourceEventColumn', value: 'sourceEventColumn' },
                        { text: 'Actions', value: 'action'},
                    ]
                    me.value.fieldDescriptors = me.value.fieldDescriptorsCqrs
                }else if (newVal == 'uimashup'){
                    me.value.viewHeaders = [
                        {
                            text: 'Key',
                            align: 'left',
                            sortable: false,
                            value: 'key',
                        },
                        { text: 'columnName', value: 'columnName' },
                        { text: 'type', value: 'type' },
                        { text: 'sourceRepository', value: 'sourceRepository',sortable: false  },
                        { text: 'sourceRepository or DirectValue', value: 'protein',sortable: false  },
                        { text: 'hateoas', value: 'hateoas' , sortable: false },
                        { text: 'link', value: 'link', sortable: false  },
                        { text: 'Actions', value: 'action', sortable: false },
                    ]

                    me.value.fieldDescriptors = me.value.fieldDescriptorsMash
                } else if(newVal == 'graphql'){

                }
            }
        },
        mounted: function () {

        },
        methods: {}
    }
</script>


