<template xmlns:v-on="http://www.w3.org/1999/xhtml">
    <div class="canvas-panel">
        <!-- UML Dialog -->

        <v-layout>
            <modal name="uml-modal" :height='"80%"' :width="'80%'">
                <class-modeler></class-modeler>
            </modal>
        </v-layout>


        <!-- Code Modal -->
        <v-layout right>
            <modal name="code-modal" :width="'80%'" :height='"auto"' scrollable>
                <v-card flat>
                    <v-card-title>
                        <v-col
                                :col="8"
                        >
                            <span class="headline">Code Preview</span>
                        </v-col>
                        <v-col
                                :col="4"
                        >
                            <v-select
                                    :items="templateList"
                                    v-model="template"
                                    label="Select Template"
                                    hide-details
                                    class="pa-0"
                            ></v-select>
                        </v-col>

                    </v-card-title>
                    <v-divider></v-divider>
                    <v-card-text style="width: auto; height: auto;">
                        <v-row
                                class="mb-6"
                                no-gutters
                        >
                            <v-col
                                    style="margin-right: 15px; border-right: 1px solid black; max-height: 700px; max-width: 400px;"
                                    id="scroll-target"
                                    class="overflow-auto"
                            >
                                <v-treeview
                                        :items.sync='treeList'
                                        :active.sync="openCode"
                                        activatable
                                        item-key="key"
                                        return-object
                                        open-all
                                        :transition="treeOpen"
                                        open-on-click
                                        transition
                                        dense
                                >
                                    <template v-slot:prepend="{ item, open }">
                                        <v-icon v-if="!item.file">
                                            {{ open ? 'mdi-folder-open' : 'mdi-folder' }}
                                        </v-icon>
                                        <v-icon v-else>
                                            {{ files[item.file] }}
                                        </v-icon>
                                    </template>
                                </v-treeview>
                            </v-col>
                            <v-col>
                                <code-viewer v-model="openCode"></code-viewer>
                            </v-col>
                        </v-row>
                    </v-card-text>
                </v-card>
            </modal>

            <opengraph ref="opengraph" focus-canvas-on-select wheelScalable :labelEditable="true"
                       :dragPageMovable="dragPageMovable" :enableContextmenu="false" :enableRootContextmenu="false"
                       :enableHotkeyCtrlC="false" :enableHotkeyCtrlV="false"
                       :enableHotkeyDelete="false" :enableHotkeyCtrlZ="false" :enableHotkeyCtrlD="false"
                       :enableHotkeyCtrlG="false" :slider="true" :movable="true" :resizable="true" :selectable="true"
                       :connectable="true" v-if="value" v-on:canvasReady="bindEvents" :autoSliderUpdate="true"
                       v-on:connectShape="onConnectShape" :imageBase="imageBase">
                <!--엘리먼트-->
                <div v-for="(element, index) in value.definition">
                    <component :is="getComponentByClassName(element._type)"
                               v-model="value.definition[index]"></component>
                </div>
                <div v-for="(element, index) in value.relation">
                    <component :is="getComponentByClassName(element._type)" v-model="value.relation[index]"></component>
                </div>
            </opengraph>

            <v-layout right>
                <v-divider></v-divider>
                <v-btn color="info" v-on:click.native="codeModalShow"
                       style="margin-top: 16px; margin-left: 5px; margin-right: 10px; font-variant-caps: normal ">
                    code Preview
                </v-btn>
                <v-btn color="info" v-on:click.native="generateZipDialog= true"
                       style="margin-top: 16px; margin-left: 5px; margin-right: 10px;">
                    Download Archive
                </v-btn>
                <v-flex xs12 sm6 style="display: inline-block; max-width:400px;">
                    <v-text-field label="Project Name" v-model="projectName" single-line
                                  @click="unselectedAll"></v-text-field>
                </v-flex>
                <text-reader :importType="'json'" @load="loadParser($event)" style="display: inline-block;"
                             :fileName.sync="projectName"></text-reader>
                <v-btn color="info" v-on:click.native="download"
                       style="margin-top: 16px; margin-left: 5px; margin-right: 10px;">SAVE
                </v-btn>
            </v-layout>


            <v-card class="tools" style="top:100px; text-align: center;">
                <span class="bpmn-icon-hand-tool" v-bind:class="{ icons : !dragPageMovable, hands : dragPageMovable }"
                      _width="30"
                      _height="30" v-on:click="toggleGrip">
                     <v-tooltip md-direction="right">Hands</v-tooltip>
                </span>
                <v-tooltip right v-for="(item, key) in elementTypes" :key="key">
                    <template v-slot:activator="{ on }">
                        <span
                                class="icons draggable"
                                align="center"
                                :_component="item.component"
                                :_width="item.width"
                                :_height="item.height">
                        <img height="30px" width="30px" :src="item.src" v-on="on">
                        </span>
                    </template>
                    <span>{{item.label}}</span>
                </v-tooltip>
            </v-card>

        </v-layout>

        <v-snackbar v-model="snackbar" :color="color" :multi-line="mode === 'multi-line'" :timeout="timeout"
                    :vertical="mode === 'vertical'">
            {{ text }}
            <v-btn dark @click="snackbar = false">
                Close
            </v-btn>
        </v-snackbar>
        <v-dialog v-model="generateZipDialog" max-width="290">
            <v-card>
                <v-card-title class="headline">Generate Zip Archive</v-card-title>
                <v-card-text>
                    <v-select
                            :items="templateList"
                            v-model="template"
                            label="Select Template"
                            hide-details
                            class="pa-0"
                    ></v-select>
                </v-card-text>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="green darken-1" text @click="generateZip()">Download</v-btn>
                    <v-btn color="red darken-1" text @click="generateZipDialog = false">Cancel</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
        <modeler-image-generator ref="modeler-image-generator"></modeler-image-generator>
    </div>
</template>

