                userId: '',
                //스낵바 옵션
                snackbar: false,
                color: 'error',
                mode: 'multi-line',
                timeout: 6000,
                text: '수정중입니다.',
                //pusher
                pusher: {},
                connectCount: 0,
                connectInfo: [],
                channel: {},
                members: [],
                show: false,
                valueTmp: {},
                pathTmp: [],
                maxWidth: 0,
                maxHeight: 0,
                gitCodeUrl: {},
                //data structure
                model: [],
                fullPathList: [],
                treeList: [],
                getCodeList: [],
                openCode: [],
                template: '',
                treeOpen: true,
                gitUrl: '',
                gitPath: [],
                gitCode: [],
                checkName: true
            }
        },
