                    return this.projectName
                }
            },

        },
        created: function () {

        },
        mounted() {
            var me = this
            this.userId = v4();

            me.$ModelingBus.$on('MoveEvent', function () {
                me.$nextTick(function () {

                    //new UndoReo
                    if (me.undoRedoArray.length > me.undoRedoStorage) {
                        me.undoRedoArray.shift();
                    } else {
                        me.currentIndex = me.currentIndex + 1
                        me.undoRedoIndex = me.currentIndex;
                    }

                    me.undoRedoArray.push(JSON.parse(JSON.stringify(me.value)));
                    // me.currentIndex = me.currentIndex + 1
                    // me.undoRedoIndex = me.currentIndex;

                    me.value.definition.forEach(function (tmp) {
                        if (tmp.selected == true) {
                        }
                    })
                })
            });

            me.$ModelingBus.$on('umlDiagram', function () {
                me.umlModalShow()
                me.$nextTick(function () {
                })
            });

            // this.userId = v4();
            // me.pusher = new Pusher('33169ca8c59c1f7f97cd', {
            //     cluster: 'ap3',
            // });

            // const channel = me.pusher.subscribe('paint');
            // channel.bind('draw', function(data) {
            //     me.value = data.newVal
            // });
            // channel.bind('draw', (data) => {
            //     const {userId: id, newVal} = data;
            //     if (me.userId !== id) {
            //         let used = false;
            //         if (newVal.name != 'class-relation') {
            //             me.value['definition'].some(function (tmp, index) {
            //                 if (tmp.elementView.id == newVal.elementView.id) {
            //                     me.value['definition'] = [
            //                         ...me.value['definition'].slice(0, index),
            //                         newVal,
            //                         ...me.value['definition'].slice(index)
            //                     ]
            //                     used = true;
            //                     return;
            //                 }
            //             })
            //             if (used == false) {
            //                 me.value.definition.push(newVal)
            //             }
            //             // me.value.definition.push(newVal)
            //         } else {
            //             me.value['relation'].some(function (tmp, index) {
            //                 if (tmp._type != 'org.uengine.uml.model.bounded') {
            //                     me.value['relation'] = [
            //                         ...me.value['relation'].slice(0, index),
            //                         element,
            //                         ...me.value['relation'].slice(index)
            //                     ]
            //                     return;
            //                 }
            //                 if (me.value['relation'].length - 1 == index) {
            //                     me.value['relation'].push(element);
            //                 }
            //             })
            //         }
            //
            //     }
            // });

            this.$nextTick(function () {
                let startTime = new Date().getTime()

                // var count = me.channel.members;

                //$nextTick delays the callback function until Vue has updated the DOM
                // (which usually happens as a result of us changing the data
                //  so make any DOM changes here
                // this.canvas.addSlider({
                //     slider: $("#canvas_slider"),
                //     width: 200,
                //     height: 300,
                //     appendTo: "body"
                // });

                this.canvas._CONFIG.FAST_LOADING = false;

                // this.canvas.updateSlider();
                //timer end

                //new UndoReo
                me.undoRedoArray.push({
                    'definition': [],
                    'relation': []
                })

                this.$refs.opengraph.printTimer(startTime, new Date().getTime());
                if (localStorage.getItem('loadData')) {
                    me.loadParser(JSON.parse(localStorage.getItem('loadData')))
                    me.projectName = localStorage.getItem('projectName')

                }
                $(document).keydown((evt) => {
                    if (evt.keyCode == 67 && (evt.metaKey || evt.ctrlKey)) {
                        this.copy();
                    } else if (evt.keyCode == 86 && (evt.ctrlKey || evt.metaKey)) {
                        this.paste();
                    } else if (evt.keyCode == 46 || evt.keyCode == 8) {
                        this.deleteActivity();
                    } else if (evt.keyCode == 90 && (evt.metaKey || evt.ctrlKey)) {
                        if (evt.shiftKey) {
                            me.undoRedo('redo');
                        } else {
                            me.undoRedo('undo');
                        }
                    }
                });
            });

            // $(window).resize(function (va) {
            // })
        },
        watch: {
            // gitUrl: function (newVal) {
            //     var me = this
            //
            //
            // },
            fullPathList: function (newVal) {
                var me = this
                me.treeList = newVal.reduce((children, value) => {
                    value.fullPath.split('/').reduce((folder, o, i) => {

                        if (i === 0) {
                            var p = folder;
                        } else {
                            var p = folder.children = folder.children || [];
                        }

                        let f = p.find(x => x.name === o.trim());

                        if (!f) {

                            f = {
                                name: o.trim(),
                                key: me.findFile(value.key, o.trim()).key,
                                file: me.findFile(value.key, o.trim()).file,
                                code: me.findFile(value.key, o.trim()).code,
                            }

                            p.push(f);
                        }

                        return f;
                    }, children);

                    return children;
                }, []);
            },
            async template() {
                var me = this
                if (me.template == 'GIT') {
                    await me.gitTemplate().then(function () {
                        me.getListSetting()

                        me.$nextTick(function () {
                            me.treeOpen = true
                        })
                    })
                } else {
                    me.getListSetting()

                    me.$nextTick(function () {
                        me.treeOpen = true
                    })
                }

            }
        },
        methods: {
            gitTemplate() {
                var me = this
                return new Promise(function (resolve, reject) {
                    me.gitUrl = window.prompt("Please Input Github Repository URL");
                    me.$manifestsPerTemplate["GIT"] = [];
                    if (!window.localStorage.getItem("gitAccessToken")) {
                        var gitAccessToken = window.prompt("Please Input Your Github AccessToken (Only Authorization)")
                        me.$http.defaults.headers.common['Authorization'] = `token ${gitAccessToken}`;
                        localStorage.setItem('gitAccessToken', gitAccessToken);
                    }

                    me.gitRepository = me.gitUrl.split('/')[me.gitUrl.split('/').length - 2]
                    me.gitProject = me.gitUrl.split('/')[me.gitUrl.split('/').length - 1]

                    me.$http.get(`https://api.github.com/repos/` + me.gitRepository + '/' + me.gitProject + `/commits`).then(function (result) {
                        me.$http.get(`https://api.github.com/repos/` + me.gitRepository + '/' + me.gitProject + `/git/trees/` + result.data[0].sha).then(async function (test) {
                            await me.setGitList(test.data, me.gitRepository)
                            resolve()
                        })
                    })
                })
            },
            loadParser: function (loadJson) {
                var me = this
                me.value = loadJson;

                me.value.relation.forEach(function (relationItem, reIdx) {
                    me.value.definition.forEach(function (sourceItem, sourceIdx) {
                        if (sourceItem.elementView.id == relationItem.sourceElement.elementView.id) {
                            relationItem.sourceElement = sourceItem
                        }
                        if (sourceItem.elementView.id == relationItem.targetElement.elementView.id) {
                            relationItem.targetElement = sourceItem
                        }
                    })
                })
                me.value.definition.forEach(function (bdItem, bdIdx) {
                    if (bdItem._type == 'org.uengine.model.BoundedContext') {
                        bdItem.aggregates.forEach(function (aggItem, agIdx) {
                            me.value.definition.forEach(function (originAggItem) {
                                if (aggItem.elementView.id == originAggItem.elementView.id) {
                                    bdItem.aggregates[agIdx] = originAggItem
                                }
                            })
                        })
                    }
                })
                me.value.definition.forEach(function (aggItem, agIdx) {
                    if (aggItem._type == "org.uengine.model.Aggregate") {

                        if (aggItem.events.length > 0) {
                            aggItem.events.forEach(function (eventItem, evIndex) {
                                me.value.definition.some(function (originEventItem) {

                                    if (eventItem.elementView.id == originEventItem.elementView.id) {
                                        aggItem.events[evIndex] = originEventItem;
                                    }
                                })
                            })
                        }

                        if (aggItem.commands.length > 0) {
                            aggItem.commands.forEach(function (commandItem, coIdx) {
                                me.value.definition.some(function (originCommandItem) {

                                    if (commandItem.elementView.id == originCommandItem.elementView.id) {
                                        aggItem.commands[coIdx] = originCommandItem;
                                    }
                                })
                            })
                        }
                        if (aggItem.policies.length > 0) {
                            aggItem.policies.forEach(function (policyItem, poIdx) {
                                me.value.definition.some(function (originPolicyItem) {

                                    if (policyItem.elementView.id == originPolicyItem.elementView.id) {
                                        aggItem.policies[poIdx] = originPolicyItem;
                                    }

                                })
                            })
                        }
                    }
                })

                // me.value.definition.forEach(function (poItem, poIdx) {
                //     if (poItem._type == "org.uengine.model.Policy") {
                //         me.value.definition.some(function (evItem) {
                //             if (evItem.elementView.id == poItem.relationEventInfo.elementView.id) {
                //                 me.value.definition[poIdx].relationEventInfo.push(evItem)
                //             }
                //             // if (evItem.elementView.id == poItem.relationEventInfo.elementView.id) {
                //             //     // policyItem = originPolicyItem
                //             //     me.value.definition[poIdx].relationEventInfo.push(evItem);
                //             // }
                //         })
                //     }
                // })
            },
            // getGitList(list,sourcePath){
            //     var me = this
            //
            //     list.forEach(function (ele) {
            //         if(ele.type != "tree"){
            //             me.gitPath.push(ele.path)
            //         }
            //     })
            //
            // },
            // getFile(ele) {
            //     var me = this
            //
            //     return new Promise(function (resolve) {
            //         if (ele.path.includes('.jpg') || ele.path.includes('.png')) {
            //             me.$http.get(ele.url).then(function (getSource) {
            //                 resolve(getSource.data.content)
            //
            //             })
            //         } else {
            //             me.$http.get(ele.url).then(function (getSource) {
            //                 resolve(Base64.decode(getSource.data.content))
            //             })
            //         }
            //     })
            // },
            setGitList(element, repository) {
                var me = this
                me.fullPathList = []
                me.getCodeList = []
                me.gitPath = []
                me.gitCode = []
                return new Promise(function (resolve) {
                    try {
                        me.$http.get(element.url + '?recursive=1').then(function (result) {
                            result.data.tree.forEach(function (ele, idx) {

                                var fileName = ele.path.split('/')[ele.path.split('/').length - 1]
                                // var source = ''
                                if (ele.type != 'tree') {
                                    me.$manifestsPerTemplate["GIT"].push('./' + ele.path)
                                    me.gitCodeUrl[ele.path] = ele.url;
                                    console.log(me.gitCodeUrl)
                                    console.log(me.$manifestsPerTemplate)
                                }

                                if (result.data.tree.length - 1 == idx) {
                                    resolve()
                                }
                            })
                        }).catch(error => console.log(error))
                    } catch (e) {
                        console.log(e)
                    }
                })


                // me.fullPathList = me.gitPath
                // me.getCodeList = me.gitCode

            },
            getListSetting() {
                var me = this
                return new Promise(async function (resolve, reject) {
                    let tmpList = []
                    me.fullPathList = []
                    me.getCodeList = []
                    me.treeList = []
                    me.model = {
                        _type: "org.uengine.model.DomainModel",
                        boundedContexts: [],
                    }

                    // return new Promise(async function (resolve, reject) {

                    // })


                    //boundedContexts 넣기
                    me.value.definition.forEach(function (item) {
                        if (item._type == 'org.uengine.model.BoundedContext') {
                            me.model.boundedContexts.push(item)
                        }
                    })

                    var modelForElements = {
                        BoundedContext: [],
                        AggregateRoot: [],
                        Aggregate: [],
                        Policy: [],
                        Event: [],
                        Command: []
                    };

                    me.model.boundedContexts.forEach(function (bound, idx) {
                        bound.portGenerated = 8081 + idx
                        modelForElements["BoundedContext"].push(bound);

                        me.model.boundedContexts[idx].aggregates.forEach(function (agg, idx) {
                            // fieldDescriptors 전처리
                            agg.aggregateRoot.fieldDescriptors.forEach(function (fd) {
                                fd.nameCamelCase = _.camelCase(fd.name)
                                fd.namePascalCase = fd.nameCamelCase.substring(0, 1).toUpperCase() + fd.nameCamelCase.substring(1);
                                if (fd.isKey)
                                    agg.aggregateRoot.keyFieldDescriptor = fd;
                            })


                            modelForElements["Aggregate"].push(agg);
                            modelForElements["AggregateRoot"].push(agg.aggregateRoot);

                            if (agg.commands)
                                agg.commands.forEach(function (co) {
                                    if(co.name == ''){
                                        me.text = "Check Element Name"
                                        me.snackbar = true
                                        me.timeout = 1000
                                        me.checkName = false
                                    }
                                    modelForElements["Command"].push(co);
                                })

                            if (agg.policies)
                                agg.policies.forEach(function (po) {
                                    if(po.name == ''){
                                        me.text = "Check Element Name"
                                        me.snackbar = true
                                        me.timeout = 1000
                                        me.checkName = false
                                    }
                                    modelForElements["Policy"].push(po);
                                })

                            if (agg.events)
                                agg.events.forEach(function (ev) {
                                    if(ev.name == ''){
                                        me.text = "Check Element Name"
                                        me.snackbar = true
                                        me.timeout = 1000
                                        me.checkName = false
                                    }
                                    modelForElements["Event"].push(ev);
                                })

                        })

                    })

                    //template Setting
                    if (me.template.length > 0) {
                        var template = me.template;
                    } else {
                        var template = 'spring-boot';
                    }

                    // 아래 데이터는 npm 빌드때 파일시스템 tree 탐색을 통해 자동으로 생성하거나 일일이 작업해줘야 한다.
                    var lastIndex = me.$manifestsPerTemplate[template].length - 1


                    me.$manifestsPerTemplate[template].forEach(function (element, index) {
                        element = element.replace('./', '')
                        var doit = function (content) {

                            if (element.includes('for-model')) {
                            }
                            var test = ''
                            var count = 0
                            if (!content.includes('forEach:')) {
                                count = count + 1
                                if (element.includes('for-model')) {
                                    var forEach = 'Model'
                                    test = test.concat('forEach: ' + forEach)
                                } else {
                                    var forEach = 'BoundedContext'
                                    test = test.concat('forEach: ' + forEach)
                                }
                            } else {
                                var forEach = content.split("forEach:")[1].split("\n")[0].trim();
                            }

                            if (!content.includes('fileName:')) {
                                count = count + 1
                                if (element.split('/')) {
                                    var fileName = element.substring(element.lastIndexOf("/") + 1);
                                    test = test.concat('\n')
                                    test = test.concat('fileName: ' + fileName)
                                }
                            }

                            if (!content.includes('path:')) {
                                var pathData = element.substring(0, element.lastIndexOf("/") + 1);
                                count = count + 1
                                if (element.includes('for-model')) {
                                    test = test.concat('\n')
                                    test = test.concat(`path: ${path.dirname(element).replace('./', '')}`)
                                } else {
                                    test = test.concat('\n')
                                    test = test.concat(`path: {{name}}/${path.dirname(element).replace('./', '')}`)
                                }
                            }

                            if (count == 3) {
                                test = test.concat('\n---\n')
                            } else {
                                test = test.concat('\n')
                            }

                            if (test.length > 0) {
                                content = test.concat(content)
                            }

                            var model = modelForElements[forEach];

                            // if(model[0]._type == 'org.uengine.model.Policy' && model[0].relationEventInfo.length > 0)

                            var process = function (ele) {
                                if (ele == undefined) {
                                    var optionsMetadataAndSource = content.split("---"); // 소스와 구분
                                    var optionsPathHeader = yamlpaser.load(optionsMetadataAndSource[0])

                                    var elementPath = element.split('/');
                                    var elementPathString = elementPath[0] + '/' + elementPath[1];
                                    if (!optionsPathHeader['path'].includes(elementPathString)) {
                                        optionsPathHeader['path'] = elementPathString.concat(optionsPathHeader['path'])
                                    }

                                    var optionsPath = optionsPathHeader['path'].split('/');
                                    var optionsPathString = '';
                                    for (var o = 1; o < optionsPath.length; o++) {
                                        if (o == 1) {
                                            optionsPathString = optionsPathString.concat(optionsPath[o]);
                                        } else {
                                            optionsPathString = optionsPathString.concat('/' + optionsPath[o]);
                                        }
                                    }
                                    //gateway2/src/main/java/com/example/template
                                    //gateway/src/main/java/com/example

                                    // FullPath : for-model/gateway/src/main/java/com/example/template/Application.java
                                    // Path: /com/example
                                    // if()
                                    var options = {
                                        "package": `${optionsPathString}`.replace(elementPath[1] + '/src/main/java/', '').replace(/\//gi, '.'),
                                        "packagePath": `${optionsPathString}`
                                    }

                                    ele = me.model

                                } else {
                                    var options = {
                                        "package": `${me.projectName}`,
                                        "packagePath": `src/main/java/${me.projectName.replace(".", "/")}`
                                    }
                                }

                                ele.options = options;

                                var gen = window.$Mustache.render(content, ele);
                                var metadataAndSource = gen.split("---"); // 소스와 구분
                                var header = yamlpaser.load(metadataAndSource[0])
                                var options = {}
                                Object.keys(header).forEach(function (keyValue, idx) {
                                    var key = keyValue
                                    var value = Object.values(header)[idx]
                                    if (key == 'path' && (value.lastIndexOf('.') == value.length - 1)) {
                                        value = value.substring(0, value.lastIndexOf(".") - 1)
                                    }
                                    options[key] = value;
                                })

                                var fileName = options['fileName']
                                var fullPath = options['path'] + '/' + options['fileName']

                                if (fileName.includes('Application'))

                                    if (elementPathString && !fullPath.includes(elementPathString)) {
                                        fullPath = elementPathString.concat(fullPath)
                                    }
                                // /src/main/java/com/example/Application.java
                                // for-model/gateway2/src/main/java/com/example/template/Application.java
                                var source = ''

                                if (metadataAndSource.length > 1) {
                                    for (var data in metadataAndSource) {

                                        if (data != 0 && data < metadataAndSource.length - 1) {
                                            source = source.concat(metadataAndSource[data] + '---\n')
                                        } else if (data == metadataAndSource.length - 1) {
                                            source = source.concat(metadataAndSource[data])
                                        }
                                    }
                                }

                                var codeValue = {}
                                var fullValue = {}
                                source = source.replace('\n', '')
                                if (source.length > 1) {
                                    if (ele._type == 'org.uengine.model.DomainModel') {
                                        // /src/main/java/com/example/Application.java
                                        // gateway2/src/main/java/com/example/template/Application.java

                                        codeValue = {
                                            'key': elementPath[1] + fileName,
                                            'fileName': fileName,
                                            'code': source,
                                            'file': me.fileType(fileName)
                                        }

                                        fullValue = {
                                            'key': elementPath[1] + fileName,
                                            'fullPath': fullPath.replace('for-model/', '')
                                        }
                                    } else {
                                        codeValue = {
                                            'key': ele.elementView.id + fileName,
                                            'fileName': fileName,
                                            'code': source,
                                            'file': me.fileType(fileName)
                                        }

                                        fullValue = {
                                            'key': ele.elementView.id + fileName,
                                            'fullPath': fullPath
                                        }
                                    }


                                    me.getCodeList.push(codeValue)
                                    me.fullPathList.push(fullValue)
                                }
                            }


                            if (Array.isArray(model)) {
                                model.forEach(process);
                            } else {
                                process(model);
                            }

                        } //end of doit


                        var xhttp = new XMLHttpRequest();
                        xhttp.onreadystatechange = function () {

                            if (this.readyState == 4 && this.status == 200) {
                                if (me.template == 'GIT') {
                                    var gitCodeObj = JSON.parse(this.responseText);
                                    var gitCodeTmp = Base64.decode(gitCodeObj.content)
                                    doit(gitCodeTmp)
                                } else {
                                    doit(this.responseText);
                                }
                                if (lastIndex == index) {
                                    resolve()
                                }
                            }
                        };
                        if (me.template == 'GIT') {
                            xhttp.open("GET", me.gitCodeUrl[element], true);
                            xhttp.send();
                        } else {
                            xhttp.open("GET", "/static/templates/" + template + "/" + element, true);
                            xhttp.send();
                        }


                    });

                })

            },
            fileType(file) {

                var type;

                if (file.includes('.java')) {
                    type = 'java'
                } else if (file.includes('Dockerfile')) {
                    type = 'docker'
                } else if (file.includes('.xml')) {
                    type = 'xml'
                } else if (file.includes('.yaml') || file.includes('.yml') || file.includes('.properties') || file.includes('mvnw') || file.includes('.groovy')) {
                    type = 'txt'
                } else if (file.includes('md')) {
                    type = 'md'
                } else if (file.includes('.jpg') || file.includes('.png') || file.includes('.jpeg')) {
                    type = 'png'
                } else if (file.includes('.json')) {
                    type = 'json'
                } else if (file.includes('.py')) {
                    type = 'python'
                }

                return type
            },
            findFile(key, name) {
                var me = this
                var ob = {}

                me.getCodeList.some(function (meta) {
                    if (meta.key == key && meta.fileName == name) {
                        ob = {
                            key: meta.key,
                            code: meta.code,
                            file: meta.file,
                        }
                    }
                })
                if (ob.key == undefined) {
                    ob = {
                        key: name,
                    }
                }

                return ob
            },
            //
            async codeModalShow() {
                var me = this
                me.getCodeList = []
                if (me.projectName.length < 1) {
                    me.projectName = window.prompt("Please input your ProjectName")
                }
                await me.getListSetting()
                if(me.checkName)
                    me.$modal.show('code-modal');

                me.checkName = true

            },
            umlModalShow() {
                this.$modal.show('uml-modal');
            },
            async generateZip() {
                var me = this

                if (me.treeList.length > 0) {
                    me.treeList.forEach(function (list) {
                        if (!list.file) {
                            me.reverse(list.children, list.name)
                        } else {
                            me.pathTmp.push({path: list.name, code: list.code})
                            // var blob = new Blob(["Hello, world!"], {type: "text/plain;charset=utf-8"});
                            // FileSaver.saveAs(blob, list.name);
                        }
                    })

                    var zip = new JSZip();

                    var parents = [];


                    me.pathTmp.forEach(function (generateData) {
                        // if(typeof generateData == String) {
                        //     // zip.file(generateData.path,)
                        // }
                        if (generateData.path.includes('/')) {
                            parents.push(generateData.path.split('/')[0])
                        }
                        zip.file(generateData.path, generateData.code)
                    })
                    // zip.file("package.json", "...");
                    // zip.file("lib/index.js", "...");
                    // zip.file("test/index.html", "...");
                    // zip.file("test/asserts/file.js", "...");
                    // zip.file("test/asserts/generate.js", "...");
                    parents.forEach(function (prefix) {
                        zip.folder(prefix).forEach(function (relativePath, file) {
                        });
                    })

                    zip.generateAsync({type: "blob"})
                        .then(function (content) {
                            // Force down of the Zip file
                            saveAs(content, `${me.projectName}.zip`);
                        });

                } else {
                    await me.getListSetting();
                    me.generateZip();
                }

            },
            reverse(item, path) {
                var me = this
                item.forEach(function (list) {
                    if (list.children) {
                        //폴더 생성하기
                        var tmpPath = path + '/' + list.name

                        me.reverse(list.children, tmpPath);
                    } else {
                        //파일생성하
                        if (list.code) {
                            me.pathTmp.push({path: path + '/' + list.name, code: list.code})
                        }
                    }
                })
            },
            codeModalhide() {
                this.$modal.hide('code-modal');
            },
            unselectedAll: function () {
                this.value.definition.forEach(function (definition) {
                    definition.selected = false
                })
                this.value.relation.forEach(function (relation) {
                    relation.selected = false
                })
            },
            ajax: function (url, method, payload, successCallback) {
                var xhr = new XMLHttpRequest();
                xhr.open(method, url, true);
                // xhr.withCredentials = true;
                xhr.setRequestHeader("Content-Type", "application/json;charset=UTF-8");
                xhr.onreadystatechange = function () {
                    if (xhr.readyState != 4 || xhr.status != 200) return;
                    successCallback(xhr.responseText);
                };
                xhr.send(JSON.stringify(payload));
            },
            connectshow: function () {
                var me = this
                if (me.show == true) {
                    me.show = false
                } else {
                    me.show = true
                }
