<template>
    <div>
        <geometry-element
                selectable
                movable
                resizable
                deletable
                :id.sync="value.elementView.id"
                :x.sync="value.elementView.x"
                :y.sync="value.elementView.y"
                :width.sync="value.elementView.width"
                :height.sync="value.elementView.height"
                :angle.sync="value.elementView.angle"
                v-on:selectShape="selectedActivity"
                v-on:deSelectShape="deSelectedActivity"
                v-on:dblclick="showProperty"
                v-on:addedToGroup="onAddedToGroup"
                :label="value.name"
                :_style="{
                'label-angle':value.elementView.angle,
                'font-weight': 'bold','font-size': '16'
                }"
        >
            <!--v-on:dblclick="$refs['dialog'].open()"-->
            <geometry-rect
                    :_style="{
          'fill-r': 1,
          'fill-cx': .1,
          'fill-cy': .1,
          'stroke-width': 1.4,
          'stroke': '#F8D454',
          fill: '#F8D454',
          'fill-opacity': 1,
          r: '1'
        }"
            >
            </geometry-rect>
            <sub-elements>
                <!--title-->
                <text-element
                        :sub-width="'100%'"
                        :sub-height="titleH"
                        :sub-top="0"
                        :sub-left="0"
                        :text="value.classReference ? value.classReference : '<< Aggregate >>'">
                </text-element>
            </sub-elements>
        </geometry-element>


        <modeling-property-panel
                v-model="value"
                :titleName="'Aggregate'"
                :img="'https://raw.githubusercontent.com/kimsanghoon1/k8s-UI/master/public/static/image/event/aggregate.png'"
        >
        </modeling-property-panel>

    </div>
</template>

<script>
    import Element from '../../modeling/Element'

    var Mustache = require('mustache')

    export default {
        mixins: [Element],
        name: 'aggregate-definition',
        props: {},
        computed: {
            namePascalCase() {
                var me = this
                return me.name.charAt(0).toUpperCase() + me.name.slice(1)
            },
            defaultStyle() {
                return {}
            },
            className() {
                return 'org.uengine.model.Aggregate'
            },
            createNew(elementId, x, y, width, height) {
                return {
                    _type: this.className(),
                    name: '',
                    namePlural: '',
                    namePascalCase: '',
                    nameCamelCase: '',
                    elementView: {
                        '_type': this.className(),
                        'id': elementId,
                        'x': x,
                        'y': y,
                        'width': 100,
                        'height': 100,
                        'style': JSON.stringify({})
                    },
                    description_text: '비즈니스 로직 처리의 도메인 객체 덩어리\n' +
                        '(서로 연결된 하나 이상의 엔터티 및 value objects의 집합체)',
                    checkValue: false,
                    drawer: false,
                    selected: false,
                    boundedContext: '',
                    aggregateRoot: {
                        _type: "org.uengine.model.ClassDefinition",
                        fieldDescriptors:
                            [
                                {
                                    _type: "org.uengine.model.FieldDescriptor",
                                    name: "id",
                                    className: "Long",
                                    nameCamelCase: 'id',
                                    namePascalCase: 'Id',
                                    isKey: true
                                }
                            ],
                    },
                    events: [],
                    commands: [],
                    policies: [],
                    viewes:[],
                }

            }
        },
        data: function () {
            return {
                itemH: 200,
                titleH: (this.value.classReference ? 60 : 30),
                reference: this.value.classReference != null,
                referenceClassName: this.value.classReference
            };
        },
        created: function () {

        },
        watch: {
            "value.name": function (newVal) {
                var me = this
                me.value.checkValue = true
            },
            "value.drawer": function (newVal) {
                var me = this
                if (newVal == false) {
                    if (me.value.checkValue) {

                        //command, event, policy 변경
                        if (me.value.events.length > 0)
                            me.value.events.forEach(function (eve) {
                                eve.aggregate = JSON.parse(JSON.stringify(me.value))
                            })

                        if (me.value.commands.length > 0)
                            me.value.commands.forEach(function (co) {
                                co.aggregate = JSON.parse(JSON.stringify(me.value))
                            })

                        if (me.value.policies.length > 0)
                            me.value.policies.forEach(function (po) {
                                po.aggregate = JSON.parse(JSON.stringify(me.value))
                            })

                        if (me.value.viewes.length > 0)
                            me.value.viewes.forEach(function (vi) {
                                vi.aggregate = JSON.parse(JSON.stringify(me.value))
                            })
                        me.value.checkValue = false
                    }
                }
            }

        },
        mounted: function () {
            var me = this
            var aggId = this.value.elementView.id

            // var $tr = $('#' + aggId);
            // $tr.parent().children().first().before($tr)

        },
        methods: {},

    }
</script>


